body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
  display: flex;
  justify-content: center;
  margin: 0px 0px;
}

p {
  display: flex;
  text-align: center;
}

#header {
  display: flex;
  justify-content: space-between;
  margin: 0px 0px;
  padding: 15px 40px;
  height: 50px;
  background-color: rgb(93, 198, 158);
}

#footer {
  background-color: blue;
}

#nav-link {
  color: white;
  font-size: 20px;
  padding: 10px 15px;
  text-decoration: none;
}

.homepage-hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: rgb(93, 198, 158);
}

.homepage {

}

.seo-hero {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: rgb(93, 198, 158);
}

.web-design-hero {
  display: flex;  
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: rgb(93, 198, 158);
}

.about-us-hero {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: rgb(93, 198, 158);
}

.contact-us-hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background-color: rgb(93, 198, 158);
}

.hero-text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vh;
  color: white;
}

.hero-sub-text {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 5px; 
  font-size: 3vh;
  color: white;
}

.call-to-action {
  display: flex;
  justify-content: space-between;
  height: 40px;
  padding: 50px 10vw;
  background-color: rgba(238, 238, 238, 0.749);
}

.call-to-action-text {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.call-to-action-number {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
 
.homepage-information {
  display: flex;
  flex-direction: column;
}

.homepage-information {
  padding: 5vw 15vh;
}

.title-and-text {
  padding: 30px 50px;
}

.get-to-know-us-container {
  display: flex;
  flex-direction: row;
  margin: 0px 0px;
  color: white;
  background-color: rgb(93, 198, 158);
}

.get-to-know-us {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 20px 150px;
  border: 2px solid blue;
}

.get-to-know-us-logo {
  display: flex;
  flex-direction: column;
  border: 2px solid red;
}

#canvas1 {
  border: 2px solid black;
  position: absolute;
  top: 350%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 706px;
} 